.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  text-align: center;
  padding: 2rem 1rem;
}

.error-icon {
  margin-bottom: 2rem;

  ion-icon {
    font-size: 4rem;
    opacity: 0.8;
  }
}

.error-title {
  color: var(--ion-color-primary);
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1rem;
  margin-top: 0;
}

.error-message {
  margin-bottom: 2.5rem;
  max-width: 400px;

  p {
    font-size: 1rem;
    line-height: 1.5;
    margin: 0;
  }
}

.loading-container {
  margin-bottom: 2.5rem;
  max-width: 400px;

  p {
    font-size: 1rem;
    line-height: 1.5;
    margin: 0;
    text-align: center;
    font-style: italic;
  }
}

.action-buttons {
  width: 100%;
  max-width: 320px;

  .action-button {
    margin-bottom: 1rem;
    --border-radius: 12px;
    height: 48px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.help-text {
  margin-top: 2rem;
  max-width: 350px;

  p {
    font-size: 0.875rem;
    line-height: 1.4;
    margin: 0;
  }
}

// Responsive adjustments
@media (max-height: 600px) {
  .error-container {
    min-height: 60vh;
    padding: 1rem;
  }

  .error-icon {
    margin-bottom: 1rem;

    ion-icon {
      font-size: 3rem;
    }
  }

  .error-title {
    font-size: 1.25rem;
    margin-bottom: 0.5rem;
  }

  .error-message {
    margin-bottom: 1.5rem;
  }

  .help-text {
    margin-top: 1rem;
  }
}
